<template>
  <b-container class="padding-top expand-width">
    <b-row>
      <b-col>
        <transition name="SearchBar">
          <b-form-input
            v-if="toggleItems"
            key="0"
            @click="toggleItemList()"
            class="style-inputField-scheme margin-b"
            placeholder="Item Name"
            autocomplete="off"
          ></b-form-input>
          <b-form-input
            v-else
            key="1"
            v-model="FilterCharacterName"
            @input="Filter($event, 4)"
            class="style-inputField-scheme margin-b"
            placeholder="Character Name"
            autocomplete="off"
          ></b-form-input>
        </transition>
        <div
          @click="toggle(index)"
          class="menu"
          v-for="(menu, index) in menus"
          :key="index"
        >
          <p
            :class="{
              MenuActive:
                (index === 0 && toggleClasses === true) ||
                (index === 1 && toggleOfficers === true) ||
                (index === 2 && toggleDate === true) ||
                (index === 3 && toggleItems === true)
            }"
          >
            {{ menu }}
          </p>
        </div>
        <p @click="Clear()" class="menu" id="Clear">Clear All</p>
      </b-col>
    </b-row>
    <hr class="bg-white" />
    <b-row>
      <transition name="Filter">
        <b-col v-if="toggleClasses">
          <div
            @click="Filter($event, 0, index)"
            v-for="(classes, index) in filerClasses"
            class="FilterWrapper"
            :key="index"
          >
            <p
              class="Filter"
              :class="{
                filterClassesActive: toggleFilterClasses[classes] === true
              }"
            >
              <span
                v-if="toggleFilterClasses[classes] === true"
                class="FilterClose"
                >X </span
              >{{ classes }}
            </p>
          </div>
          <div
            @click="Filter($event, 0, 'invertClasses')"
            class="cursor-pointer text-teal-500 hover:text-teal-800 FilterInvertButton"
          >
            <Rotate3dVariant />
          </div>
        </b-col>
      </transition>

      <transition name="Filter">
        <b-col v-if="toggleOfficers">
          <div
            @click="Filter($event, 1, index)"
            class="FilterWrapper"
            v-for="(guildOfficers, index) in filterGuildOfficers"
            :key="index"
          >
            <p
              class="Filter"
              :class="{
                filterClassesActive:
                  (index === 0 && toggleFilterOfficers.GuildLead === true) ||
                  (index === 1 && toggleFilterOfficers.ClassOfficer === true) ||
                  (index === 2 && toggleFilterOfficers.None === true)
              }"
            >
              <span
                v-if="
                  (index === 0 && toggleFilterOfficers.GuildLead === true) ||
                    (index === 1 &&
                      toggleFilterOfficers.ClassOfficer === true) ||
                    (index === 2 && toggleFilterOfficers.None === true)
                "
                class="FilterClose"
                >X </span
              >{{ guildOfficers }}
            </p>
          </div>
          <div
            @click="Filter($event, 1, 'invertOfficers')"
            class="cursor-pointer text-teal-500 hover:text-teal-800 FilterInvertButton"
          >
            <Rotate3dVariant />
          </div>
        </b-col>
      </transition>
      <transition name="Filter">
        <b-col v-if="toggleDate">
          <div
            @click="Filter($event, 2, index)"
            class="FilterWrapper"
            v-for="(date, index) in filterDate"
            :key="index"
          >
            <p
              class="Filter"
              :class="{
                filterClassesActive:
                  (index === 0 && toggleFilterDate.January === true) ||
                  (index === 1 && toggleFilterDate.February === true) ||
                  (index === 2 && toggleFilterDate.March === true) ||
                  (index === 3 && toggleFilterDate.April === true) ||
                  (index === 4 && toggleFilterDate.May === true) ||
                  (index === 5 && toggleFilterDate.June === true) ||
                  (index === 6 && toggleFilterDate.July === true) ||
                  (index === 7 && toggleFilterDate.August === true) ||
                  (index === 8 && toggleFilterDate.September === true) ||
                  (index === 9 && toggleFilterDate.October === true) ||
                  (index === 10 && toggleFilterDate.November === true) ||
                  (index === 11 && toggleFilterDate.December === true)
              }"
            >
              <span class="FilterClose">X</span>
              {{ date }}
            </p>
          </div>
          <div
            @click="Filter($event, 2, 'invertDate')"
            class="cursor-pointer text-teal-500 hover:text-teal-800 FilterInvertButton"
          >
            <Rotate3dVariant />
          </div>
        </b-col>
      </transition>
      <transition name="Filter">
        <b-col v-if="toggleItems">
          <itemList
            v-if="itemListOn && toggleItems"
            v-bind:openAddItem="false"
            v-on:closeItemList="closeItemList"
            v-on:filterItem="filterItem"
          />
          <div
            @click="Filter($event, 3, index)"
            class="FilterWrapper"
            v-for="(item, index) in itemsToFilter"
            :key="index"
          >
            <p class="Filter filterClassesActive">
              <span class="FilterClose">X</span>
              {{ item }}
            </p>
          </div>
          <p
            v-if="itemsToFilter.length > 0"
            @click="Clear()"
            class="menu"
            id="Clear"
          >
            Clear
          </p>
        </b-col>
      </transition>
    </b-row>

    <b-row class="padding-top">
      <transition-group name="Kachel">
        <b-col
          md="4"
          v-for="(character, index) in characters"
          :key="index + 0"
          class="mb-3"
        >
          <div
            class="card-info p-2 flex rounded-t-lg justify-between items-center bg-gray-100 border-b-1"
            :class="{
              'rounded-b-lg': expandItems[index].expandItems === false
            }"
          >
            <div>
              <img
                v-if="character.class === 'warriors'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 warriors"
                :src="require('@/assets/icons/classes/Warrior.png')"
              />
              <img
                v-if="character.class === 'priests'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 priests"
                :src="require('@/assets/icons/classes/Priest.png')"
              />
              <img
                v-if="character.class === 'paladins'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 paladins"
                :src="require('@/assets/icons/classes/Paladin.png')"
              />
              <img
                v-if="character.class === 'hunters'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 hunters"
                :src="require('@/assets/icons/classes/Hunter.png')"
              />
              <img
                v-if="character.class === 'rogues'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 rogues"
                :src="require('@/assets/icons/classes/Rogue.png')"
              />
              <img
                v-if="character.class === 'mages'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 mages"
                :src="require('@/assets/icons/classes/Mage.png')"
              />
              <img
                v-if="character.class === 'warlocks'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 warlocks"
                :src="require('@/assets/icons/classes/Warlock.png')"
              />
              <img
                v-if="character.class === 'druids'"
                class="w-12 h-12 rounded-full border-2 shadow bg-gray-700 druids"
                :src="require('@/assets/icons/classes/Druid.png')"
              />
            </div>
            <div class="flex items-center">
              <crown v-if="character.guildlead" :size="20" />
              <crownOutline v-if="character.id === 0" :size="20" />
              <p
                class="ml-2 text-gray-900 font-semibold font-sans tracking-wide"
              >
                {{ character.name }}
              </p>
            </div>
            <div class="m-2 text-teal-500 hover:text-teal-800">
              <subdirectoryArrowLeft @click="toggleExpandItems(index)" />
            </div>
          </div>
          <transition name="fade">
            <div
              v-if="expandItems[index].expandItems === true"
              class="card-add rounded-b-lg bg-gray-200 px-4 items"
            >
              <vue-custom-scrollbar
                class="scroll-area"
                :settings="scrollSettings"
              >
                <div
                  v-for="(item, itemIndex) in character.items"
                  :key="itemIndex"
                >
                  <a
                    v-if="checkDate(item)"
                    v-bind:data-wowhead="'domain=classic&item=' + item.id"
                    class="flex border-b-2 cursor-default"
                  >
                    <span
                      v-if="item.name.length < 30"
                      class="text-gray-900 font-sans"
                    >
                      {{ item.name }}
                    </span>
                    <span v-else class="text-gray-900 font-sans">
                      {{ item.name.substr(0, 30) + "..." }}
                    </span>
                  </a>
                </div>
              </vue-custom-scrollbar>
            </div>
          </transition>
        </b-col>
      </transition-group>
    </b-row>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import crown from "vue-material-design-icons/Crown.vue";
import crownOutline from "vue-material-design-icons/CrownOutline.vue";
import vueCustomScrollbar from "vue-custom-scrollbar";
import subdirectoryArrowLeft from "vue-material-design-icons/ChevronDoubleDown.vue";
import Rotate3dVariant from "vue-material-design-icons/Rotate3dVariant.vue";
import itemList from "@/components/itemList.vue";

export default {
  name: "home",
  components: {
    crown,
    crownOutline,
    vueCustomScrollbar,
    subdirectoryArrowLeft,
    Rotate3dVariant,
    itemList
  },
  data() {
    return {
      menus: ["Classes", "Officers", "Date", "Items"],
      filerClasses: [
        "Warriors",
        "Priests",
        "Paladins",
        "Hunters",
        "Rogues",
        "Mages",
        "Warlocks",
        "Druids"
      ],
      filterDate: [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ],
      filterGuildOfficers: ["Guildlead", "Class Officer", "None"],
      FilterCharacterName: "",
      toggleClasses: true,
      toggleDate: false,
      toggleOfficers: false,
      toggleItems: false,
      characters: [],
      toggleFilterClasses: {
        Warriors: true,
        Priests: true,
        Paladins: true,
        Hunters: true,
        Rogues: true,
        Mages: true,
        Warlocks: true,
        Druids: true
      },
      toggleFilterOfficers: {
        GuildLead: true,
        ClassOfficer: true,
        None: true
      },
      toggleFilterDate: {
        January: true,
        February: true,
        March: true,
        April: true,
        May: true,
        June: true,
        July: true,
        August: true,
        September: true,
        October: true,
        November: true,
        December: true
      },
      scrollSettings: {
        minScrollbarLength: 40,
        maxScrollbarLength: 60,
        suppressScrollX: true,
        wheelPropagation: false
      },
      expandItems: [],
      itemListOn: false,
      itemsToFilter: ["All"]
    };
  },
  computed: {
    ...mapGetters(["GetWarriors"]),
    ...mapGetters(["GetPriests"]),
    ...mapGetters(["GetPaladins"]),
    ...mapGetters(["GetHunters"]),
    ...mapGetters(["GetRogues"]),
    ...mapGetters(["GetMages"]),
    ...mapGetters(["GetWarlocks"]),
    ...mapGetters(["GetDruids"])
  },
  mounted() {
    let recaptchaScript = document.createElement("script");
    recaptchaScript.setAttribute(
      "src",
      "https://wow.zamimg.com/widgets/power.js"
    );
    document.head.appendChild(recaptchaScript);
  },
  created() {
    this.fetchAllClasses();
  },
  methods: {
    //Fetch all classes
    ...mapActions(["FetchWarriors"]),
    ...mapActions(["FetchPriests"]),
    ...mapActions(["FetchPaladins"]),
    ...mapActions(["FetchHunters"]),
    ...mapActions(["FetchRogues"]),
    ...mapActions(["FetchMages"]),
    ...mapActions(["FetchWarlocks"]),
    ...mapActions(["FetchDruids"]),

    async fetchAllClasses() {
      this.characters = [];
      this.expandItems = [];
      await this.FetchWarriors().then(async result => {
        await this.GetWarriors.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchPriests().then(async result => {
        await this.GetPriests.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchPaladins().then(async result => {
        await this.GetPaladins.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchHunters().then(async result => {
        await this.GetHunters.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchRogues().then(async result => {
        await this.GetRogues.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchMages().then(async result => {
        await this.GetMages.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchWarlocks().then(async result => {
        await this.GetWarlocks.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
      this.FetchDruids().then(async result => {
        await this.GetDruids.forEach(element => {
          this.characters.push(element);
          this.expandItems.push({ expandItems: false });
        });
        return result;
      });
    },

    toggle(index) {
      if (index === 0) {
        if (this.toggleClasses) {
          this.toggleClasses = false;
        } else {
          this.toggleClasses = true;
          this.toggleDate = false;
          this.toggleOfficers = false;
          this.toggleItems = false;
        }
      } else if (index === 1) {
        if (this.toggleOfficers) {
          this.toggleOfficers = false;
        } else {
          this.toggleOfficers = true;
          this.toggleClasses = false;
          this.toggleDate = false;
          this.toggleItems = false;
        }
      } else if (index === 2) {
        if (this.toggleDate) {
          this.toggleDate = false;
        } else {
          this.toggleDate = true;
          this.toggleClasses = false;
          this.toggleOfficers = false;
          this.toggleItems = false;
        }
      } else if (index === 3) {
        if (this.toggleItems) {
          this.toggleItems = false;
        } else {
          this.toggleItems = true;
          this.toggleClasses = false;
          this.toggleDate = false;
          this.toggleOfficers = false;
        }
      }
    },

    Clear() {
      // Set all toggle options to default
      this.toggleClasses = true;
      this.toggleDate = false;
      this.toggleOfficers = false;
      this.toggleItems = false;

      this.toggleFilterClasses.Warriors = true;
      this.toggleFilterClasses.Priests = true;
      this.toggleFilterClasses.Paladins = true;
      this.toggleFilterClasses.Hunters = true;
      this.toggleFilterClasses.Rogues = true;
      this.toggleFilterClasses.Mages = true;
      this.toggleFilterClasses.Warlocks = true;
      this.toggleFilterClasses.Druids = true;

      this.toggleFilterOfficers.GuildLead = true;
      this.toggleFilterOfficers.ClassOfficer = true;
      this.toggleFilterOfficers.None = true;

      this.toggleFilterDate.January === true;
      this.toggleFilterDate.February === true;
      this.toggleFilterDate.March === true;
      this.toggleFilterDate.April === true;
      this.toggleFilterDate.May === true;
      this.toggleFilterDate.June === true;
      this.toggleFilterDate.July === true;
      this.toggleFilterDate.August === true;
      this.toggleFilterDate.September === true;
      this.toggleFilterDate.October === true;
      this.toggleFilterDate.November === true;
      this.toggleFilterDate.December === true;

      this.characters = [];
      this.expandItems = [];
      // Load all characters from store -> loading from firebase only with refresh
      this.GetWarriors.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetPriests.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetPaladins.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetHunters.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetRogues.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetMages.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetWarlocks.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
      this.GetDruids.forEach(element => {
        this.characters.push(element);
        this.expandItems.push({ expandItems: false });
      });
    },

    //Filter entry
    Filter(el, Filter, index) {
      let cache = [];
      this.expandItems = [];

      //Only the Filter gets an index to toggle the choosen options
      //Filter Classes triggered
      if (Filter === 0) {
        cache = this.filterClasses(index);
        cache = this.filterOfficers(cache);
        cache = this.FilterCharacterNames(cache);
      } //Filter Officers triggered
      else if (Filter === 1) {
        cache = this.filterClasses();
        cache = this.filterOfficers(cache, index);
        cache = this.FilterCharacterNames(cache);
      } //Filter Date triggered
      else if (Filter === 2) {
        cache = this.filterClasses();
        cache = this.filterOfficers(cache);
        this.filterDates(index); //Only here to trigger true/false statement
        cache = this.FilterCharacterNames(cache);
      }
      // Filter 3 = Items -> To Do
      else if (Filter === 4) {
        cache = this.filterClasses();
        cache = this.filterOfficers(cache);
        cache = this.FilterCharacterNames(cache);
      }
      cache.forEach(element => {
        this.expandItems.push({ expandItems: false });
        return element;
      });
      this.characters = cache;
    },

    FilterCharacterNames(array) {
      let filter = [];

      array.forEach(element => {
        if (
          element.name
            .toLowerCase()
            .includes(this.FilterCharacterName.toLowerCase())
        )
          filter.push(element);
      });

      return filter;
    },
    //Filter by Class
    filterClasses(index) {
      let filter = [];

      if (index === 0 || index === "invertClasses") {
        this.toggleFilterClasses.Warriors = !this.toggleFilterClasses.Warriors;
      }
      if (index === 1 || index === "invertClasses") {
        this.toggleFilterClasses.Priests = !this.toggleFilterClasses.Priests;
      }
      if (index === 2 || index === "invertClasses") {
        this.toggleFilterClasses.Paladins = !this.toggleFilterClasses.Paladins;
      }
      if (index === 3 || index === "invertClasses") {
        this.toggleFilterClasses.Hunters = !this.toggleFilterClasses.Hunters;
      }
      if (index === 4 || index === "invertClasses") {
        this.toggleFilterClasses.Rogues = !this.toggleFilterClasses.Rogues;
      }
      if (index === 5 || index === "invertClasses") {
        this.toggleFilterClasses.Mages = !this.toggleFilterClasses.Mages;
      }
      if (index === 6 || index === "invertClasses") {
        this.toggleFilterClasses.Warlocks = !this.toggleFilterClasses.Warlocks;
      }
      if (index === 7 || index === "invertClasses") {
        this.toggleFilterClasses.Druids = !this.toggleFilterClasses.Druids;
      }

      if (this.toggleFilterClasses.Warriors) {
        this.GetWarriors.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Priests) {
        this.GetPriests.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Paladins) {
        this.GetPaladins.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Hunters) {
        this.GetHunters.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Rogues) {
        this.GetRogues.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Mages) {
        this.GetMages.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Warlocks) {
        this.GetWarlocks.forEach(element => {
          filter.push(element);
        });
      }
      if (this.toggleFilterClasses.Druids) {
        this.GetDruids.forEach(element => {
          filter.push(element);
        });
      }

      return filter;
    },

    filterOfficers(array, index) {
      let filter = [];

      if (index === 0 || index === "invertOfficers") {
        this.toggleFilterOfficers.GuildLead = !this.toggleFilterOfficers
          .GuildLead;
      }
      if (index === 1 || index === "invertOfficers") {
        this.toggleFilterOfficers.ClassOfficer = !this.toggleFilterOfficers
          .ClassOfficer;
      }
      if (index === 2 || index === "invertOfficers") {
        this.toggleFilterOfficers.None = !this.toggleFilterOfficers.None;
      }

      array.forEach(element => {
        if (
          (this.toggleFilterOfficers.GuildLead && element.guildlead) ||
          (this.toggleFilterOfficers.ClassOfficer && element.id === 0) ||
          (this.toggleFilterOfficers.None &&
            !element.guildlead &&
            element.id > 0)
        ) {
          filter.push(element);
        }
      });

      return filter;
    },

    filterDates(index) {
      if (index === 0 || index === "invertDate") {
        this.toggleFilterDate.January = !this.toggleFilterDate.January;
      }
      if (index === 1 || index === "invertDate") {
        this.toggleFilterDate.February = !this.toggleFilterDate.February;
      }
      if (index === 2 || index === "invertDate") {
        this.toggleFilterDate.March = !this.toggleFilterDate.March;
      }
      if (index === 3 || index === "invertDate") {
        this.toggleFilterDate.April = !this.toggleFilterDate.April;
      }
      if (index === 4 || index === "invertDate") {
        this.toggleFilterDate.May = !this.toggleFilterDate.May;
      }
      if (index === 5 || index === "invertDate") {
        this.toggleFilterDate.June = !this.toggleFilterDate.June;
      }
      if (index === 6 || index === "invertDate") {
        this.toggleFilterDate.July = !this.toggleFilterDate.July;
      }
      if (index === 7 || index === "invertDate") {
        this.toggleFilterDate.August = !this.toggleFilterDate.August;
      }
      if (index === 8 || index === "invertDate") {
        this.toggleFilterDate.September = !this.toggleFilterDate.September;
      }
      if (index === 9 || index === "invertDate") {
        this.toggleFilterDate.October = !this.toggleFilterDate.October;
      }
      if (index === 10 || index === "invertDate") {
        this.toggleFilterDate.November = !this.toggleFilterDate.November;
      }
      if (index === 11 || index === "invertDate") {
        this.toggleFilterDate.December = !this.toggleFilterDate.December;
      }
    },

    toggleExpandItems(index) {
      this.expandItems[index].expandItems = !this.expandItems[index]
        .expandItems;
    },

    toggleItemList() {
      this.itemListOn = !this.itemListOn;
    },

    filterItem(event) {
      let itemFound = false;
      this.itemsToFilter.forEach(element => {
        if (element === event.itemName) {
          itemFound = true;
        }
      });
      if (!itemFound) {
        this.itemsToFilter.push(event.itemName);
      }
    },

    //close ItemList
    closeItemList() {
      this.itemListOn = false;
    },

    //Check if item is in date range
    checkDate(item) {
      let moment = require("moment");
      let itemFound = false;
      let monthsToIterate = [];

      if (this.toggleFilterDate.January) {
        monthsToIterate.push(0);
      }
      if (this.toggleFilterDate.February) {
        monthsToIterate.push(1);
      }
      if (this.toggleFilterDate.March) {
        monthsToIterate.push(2);
      }
      if (this.toggleFilterDate.April) {
        monthsToIterate.push(3);
      }
      if (this.toggleFilterDate.May) {
        monthsToIterate.push(4);
      }
      if (this.toggleFilterDate.June) {
        monthsToIterate.push(5);
      }
      if (this.toggleFilterDate.July) {
        monthsToIterate.push(6);
      }
      if (this.toggleFilterDate.August) {
        monthsToIterate.push(7);
      }
      if (this.toggleFilterDate.September) {
        monthsToIterate.push(8);
      }
      if (this.toggleFilterDate.October) {
        monthsToIterate.push(9);
      }
      if (this.toggleFilterDate.November) {
        monthsToIterate.push(10);
      }
      if (this.toggleFilterDate.December) {
        monthsToIterate.push(11);
      }

      for (let index = 0; index < monthsToIterate.length; index++) {
        if (
          moment(item.date, "DD/MM/YYYY").get("M") === monthsToIterate[index]
        ) {
          itemFound = true;
          break;
        }
      }
      return itemFound;
      //To do
    }
  }
};
</script>

