import db from "@/firebase/firebaseInit.js";

const state = {};

const getters = {};

const actions = {
  //Delete character by class + name
  async deleteCharacter(state, payload) {
    if (payload) {
      await db
        .collection(payload.class)
        .where("name", "==", payload.name)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete();
          });
        });
    } else {
      alert("An error occured in deleteCharacter()");
    }
  },

  //Add Character by class
  async addCharacter(state, payload) {
    if (payload) {
      await db.collection(payload.class).add({
        id:payload.id,
        class:payload.class,
        name: payload.name,
        guildlead: payload.guildlead,
        items: payload.items
      });
    } else {
      alert("An error occured in addCharacter()");
    }
  },

  //Save characters by class
  async saveCharacters(state, payload) {
    if (payload) {
      await db
        .collection(payload.class)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete();
          });
        })
        .then(async Response => {
          for (let index = 0; index < payload.characters.length; index++) {
            await db.collection(payload.class).add({
              id:index,
              class:payload.class,
              name: payload.characters[index].name,
              guildlead: payload.characters[index].guildlead,
              items: payload.characters[index].items
            });
          }
          return Response;
        });
    } else {
      alert("An error occured in saveCharacters()");
    }
  },

  //Update existing class
  async updateCharacter(state, payload) {
    if (payload) {
      await db
        .collection(payload.class)
        .where("name", "==", payload.character.name)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              name: payload.character.name,
              guildlead: payload.character.guildlead,
              items: payload.character.items
            });
          });
        });
    } else {
      alert("An error occured in updateCharacter()");
    }
  }
};

const mutations = {};

export default {
  state,
  getters,
  actions,
  mutations
};
