<template>
  <b-navbar toggleable="lg" type="dark">
    <b-navbar-brand href="/">
      Guildname
    </b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav
        ><b-nav-item exact :to="{ name: 'home' }">Home</b-nav-item></b-navbar-nav
      >

      <b-navbar-nav class="ml-auto">
        <b-nav-item
          v-if="isloggedIn"
          class="style-color-scheme"
          id="Username"
          right
          >{{ currentUser }}</b-nav-item
        >
        <b-nav-item v-if="isloggedIn" right exact :to="{ name: 'dashboard' }"
          >Dashboard
        </b-nav-item>
        <b-nav-item v-if="!isloggedIn" right exact :to="{ name: 'login' }"
          >Login</b-nav-item
        >
        <b-nav-item v-else v-on:click="logout()" right>Logout</b-nav-item>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Header",
  data() {
    return {
      isloggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isloggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push("/login");
        });
    }
  }
};
</script>
