<template>
  <b-container id="itemList">
    <addItem
      v-if="toggleAddItem"
      :itemName="itemNameToAdd"
      :itemID="itemIDToAdd"
      :itemQuality="itemQualityToAdd"
      v-on:cancelAddItem="cancelAddItem"
      v-on:addItem="addItem"
    />
    <b-row>
      <b-col md="4" />
      <b-col md="4"
        ><h2
        v-if="characterName !== ''|| characterName !==null"
          id="itemListHeader"
          :class="{
            warriors: characterClass === 'warriors',
            priests: characterClass === 'priests',
            paladins: characterClass === 'paladins',
            hunters: characterClass === 'hunters',
            rogues: characterClass === 'rogues',
            mages: characterClass === 'mages',
            warlocks: characterClass === 'warlocks',
            druids: characterClass === 'druids'
          }"
        >
          {{ characterName }}
        </h2>
      </b-col>
      <b-col md="3" />
      <b-col md="1"
        ><button
          aria-label="Close Window"
          @click="closeItemList()"
          id="itemListClose"
        >
          <close /></button
      ></b-col>
    </b-row>

    <b-row id="itemListInput" class="padding">
      <b-col md="12">
        <b-form-input
          @input="loadItems(defaultItemsOnLoad, true)"
          class="style-inputField-scheme"
          v-model="SearchItem"
          placeholder="Item Name or ID"
          autocomplete="off"
        ></b-form-input>
      </b-col>
    </b-row>
    <b-row>
      <b-col md="12" v-if="loading"><p>Loading...</p> </b-col>
    </b-row>
    <b-row v-if="!loading" id="items">
      <vue-custom-scrollbar
        @ps-scroll-y="onScroll"
        class="scroll-area"
        :settings="scrollSettings"
      >
        <b-col md="6" v-for="item in Items" :key="item.key" class="itemLoop">
          <a
            class="item"
            @click="sendItem(item)"
            v-bind:data-wowhead="'domain=classic&item=' + item.item.id"
          >
            <img :src="item.media.assets[0].value" class="itemIMG" />
            <p
              class="itemName"
              :class="{
                qualityRar: item.item.quality.type === 'RARE',
                qualityEpic: item.item.quality.type === 'EPIC',
                qualityLegendary: item.item.quality.type === 'LEGENDARY'
              }"
            >
              <span v-if="item.item.name.length < 20">
                {{ item.item.name }}
              </span>
              <span v-else>
                {{ item.item.name.substr(0, 20) + "..." }}
              </span>
              <span class="itemID"> #{{ item.item.id }} </span>
            </p>
          </a>
        </b-col>
      </vue-custom-scrollbar>
    </b-row>
  </b-container>
</template>

<script>
import db from "@/assets/items/db.json";
import vueCustomScrollbar from "vue-custom-scrollbar";
import close from "vue-material-design-icons/Close.vue";
import addItem from "./addItem.vue";

export default {
  name: "itemList",
  props: {
    characterName: { type: String },
    characterClass: {
      type: String
    },
    openAddItem: {
      type: Boolean
    }
  },
  data() {
    return {
      loading: true,
      SearchItem: "",
      loadedItems: 0,
      Items: [],
      scrollSettings: {
        minScrollbarLength: 40,
        maxScrollbarLength: 60,
        suppressScrollX: true,
        wheelPropagation: false
      },
      defaultItemsOnLoad: 20,
      toggleAddItem: false,
      itemNameToAdd: "",
      itemIDToAdd: "",
      itemQualityToAdd: "",
      whTooltips: { colorLinks: true, iconizeLinks: true, renameLinks: true }
    };
  },
  components: {
    vueCustomScrollbar,
    close,
    addItem
  },
  mounted() {
    let recaptchaScript = document.createElement("script");
    recaptchaScript.setAttribute(
      "src",
      "https://wow.zamimg.com/widgets/power.js"
    );
    document.head.appendChild(recaptchaScript);
  },
  created() {
    this.loadItems(this.defaultItemsOnLoad);
  },
  methods: {
    loadItems(number, SearchItemChange) {
      this.loading = true;
      if (SearchItemChange) {
        this.loadedItems = 0;
        this.Items = [];
      }
      //If searched item gets changed reset all loaded items
      for (let index = 0; index < number; index++) {
        if (this.loadedItems >= Object.keys(db).length) {
          break;
        } else {
          if (this.SearchItem !== "" && this.SearchItem !== null) {
            if (
              db[this.loadedItems].item.name
                .toLowerCase()
                .includes(this.SearchItem.toLowerCase()) ||
              db[this.loadedItems].item.id.toString().includes(this.SearchItem)
            ) {
              this.Items.push(db[this.loadedItems]);
            } else {
              index--;
            }
          } else {
            this.Items.push(db[this.loadedItems]);
          }
          this.loadedItems++;
        }
      }
      this.loading = false;
    },
    onScroll: function(evt) {
      let post = evt.target;
      let st = post.scrollTop;
      let sh = post.scrollHeight;
      let ch = post.clientHeight;

      let percent = Math.floor((st / (sh - ch)) * 100);
      if (percent > 75) {
        this.loadItems(this.defaultItemsOnLoad);
      }
    },
    closeItemList() {
      this.$emit("closeItemList");
    },
    sendItem(item) {
      if (this.openAddItem) {
        this.toggleAddItem = true;
        this.itemNameToAdd = item.item.name;
        this.itemIDToAdd = item.item.id;
        this.itemQualityToAdd = item.item.quality.type;
      }else{
        this.$emit("filterItem",{
          itemName:item.item.name
        })
      }
    },
    cancelAddItem() {
      this.toggleAddItem = false;
    },
    addItem(evt) {
      this.$emit("addItemFinal", {
        characterName: this.characterName,
        characterClass: this.characterClass,
        itemID: evt.itemID,
        itemName: evt.itemName,
        date: evt.date,
        dkp: evt.dkp
      });
      this.toggleAddItem = false;
    }
  }
};
</script>

<style scoped>
#itemList {
  display: inline-block;
  position: fixed;
  max-width: 800px;
  width: auto;
  height: 600px;
  z-index: 10;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2em;
  background-color: #404040;
  border: solid black 2px;
  box-shadow: 0 2px 5px rgba(10, 10, 10, 0.05), 0 7px 20px rgba(23, 24, 26, 0.2);
}
#itemListClose {
  display: inline-block;
  color: red;
  text-align: right;
}
#itemListHeader {
  font-weight: bold;
  font-size: 1.5em;
  text-align: center;
}
.scroll-area {
  height: 400px;
}
.item:hover > * {
  border-color: #d98f40;
}
.itemLoop {
  display: inline-block;
  padding: 4px;
}
.itemIMG {
  display: inline-block;
  background-color: #303030;
  border: 2px solid black;
  box-shadow: 0 5px 10px rgba(10, 10, 10, 0.05),
    0 15px 40px rgba(23, 24, 26, 0.2);
  transition: 0.2s;
  max-width: 56px;
  width: auto;
  height: auto;
}
.itemName {
  display: inline-block;
  margin: 2px;
  padding: 2px;
  background-color: #303030;
  width: 80%;
  border: solid black;
  border-width: 2px 2px 2px 2px;
  box-shadow: 0 2px 5px rgba(10, 10, 10, 0.05), 0 7px 20px rgba(23, 24, 26, 0.2);
  transition: 0.2s;
}
.itemID {
  color: white;
}
</style>
