<template>
  <b-container id="addItem">
    <b-row>
      <b-col md="12">
        <h3 id="addItemName" :class="{
                qualityRar: itemQuality === 'RARE',
                qualityEpic: itemQuality === 'EPIC',
                qualityLegendary: itemQuality === 'LEGENDARY'
              }">{{ itemName }}</h3>
      </b-col>
    </b-row>
    <b-row class="padding">
      <b-col md="12">
        <Datepicker v-model="date" placeholder="Select Date" id="datePicker" required></Datepicker>
      </b-col>
    </b-row>
    <b-row class="padding">
      <b-col md="12">
        <b-form-input
          v-model="dkp"
          autocomplete="off"
          placeholder="DKP spend"
          required
        ></b-form-input>
      </b-col>
    </b-row>
    <b-row class="padding">
      <b-col md="6">
        <button
          class="style-button-scheme"
          aria-label="Add item"
          @click="addItem()"
          id="addItemButton"
        >
          Add
        </button>
      </b-col>
      <b-col md="6">
        <button
          class="style-button-scheme"
          aria-label="Cancel"
          @click="cancelAddItem()"
          id="cancelButton"
        >
          Cancel
        </button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import Datepicker from 'vuejs-datepicker';

export default {
  name: "addItem",
  props: {
    itemName: {
      type: String
    },
    itemID: {
      type: Number
    },
    itemQuality:{
      type:String
    }
  },
  data() {
    return {
      date: new Date(),
      options: {
        useCurrent: true,
        showClear: true,
        showClose: true
      },
      dkp: "0"
    };
  },
  components:{
    Datepicker
  },
  methods: {
    cancelAddItem() {
      this.$emit("cancelAddItem");
    },
    addItem() {
      let moment = require("moment");
      
      this.$emit("addItem", {
        itemID: this.itemID,
        itemName: this.itemName,
        date: moment(this.date).format("DD/MM/YYYY"),
        dkp: this.dkp
      });
    }
  }
};
</script>

<style scoped>
#addItem {
  position: absolute;
  z-index: 100;
  width: 55%;
  height: 55%;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.5em;
  background-color: #303030;
  border: solid black 1px;
  box-shadow: 0 2px 5px rgba(10, 10, 10, 0.05), 0 7px 20px rgba(23, 24, 26, 0.2);
}
#addItemName {
  text-align: center;
  font-size: 1.2em;
}
#addItem {
}
#canel {
}
#datePicker{
  width: 100%!important;
  height: auto;
}
</style>
